html {
  height: 100%;
}
html body {
  padding: 0px;
  margin: 0px;
  font-size: 16px;
  height: 100%;
}
html body .modal-wrap {
  display: table;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
html body .modal-wrap .modal-content-wrap {
  background-color: #2e2e2c;
  display: table-cell;
  vertical-align: middle;
  text-align: center;
}
html body .modal-wrap .modal-content-wrap .modal-content {
  display: inline-block;
  border: 1px solid #f9db00;
  /*background-color: orange;*/
  color: #fff;
}
